import { Component, OnInit, Input } from '@angular/core';
import { AggiornaService } from '../aggiorna.service';

@Component({
  selector: 'app-view2',
  templateUrl: './view2.component.html',
  styleUrls: ['./view2.component.scss']
})
export class View2Component implements OnInit {
  @Input()
  data = 'vista2';
  constructor(private observer: AggiornaService) { }

  ngOnInit() {
    this.update();
  }
  update() {

    this.observer.syncRequest.subscribe((d: string) => { this.data = d; });
  }
  ciccio() {
    this.observer.syncRequest.emit(this.data);
  }
}
