import { AggiornaService } from './../aggiorna.service';
import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-view1',
  templateUrl: './view1.component.html',
  styleUrls: ['./view1.component.scss']
})
export class View1Component implements OnInit {
  @Input()
  data = 'test';
  constructor(private observer: AggiornaService) { }

  ngOnInit() {
    this.observer.syncRequest.subscribe((d: string) => { this.data = d; });
  }

  update() {
    console.log('entro update view 1');
    this.observer.syncRequest.emit(this.data);
    console.log('esco update view 1');

  }

}
