import { AggiornaService } from './../aggiorna.service';
import { HttpClient } from '@angular/common/http';
import { Users } from './../users';
import { TransportService } from './../transport.service';
import { Component, OnInit, Input } from '@angular/core';
import { environment } from 'src/environments/environment';
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit {
  @Input()
  user: Users;
  constructor(private getUser: TransportService, private http: HttpClient, private sync: AggiornaService) { }

  ngOnInit() {
    console.log('io ascolto da users');
    this.getUser.userSelected.subscribe((user: Users) => {
      console.log('io ascolto da users  ', user);
      this.user = user;

    });
  }

  add() {
    const addURL = environment.apiUrl;
    console.log('inserting');
    // console.log('deleteURL  ', delURL);
    this.http.post(addURL, this.user);
    // this.sync.syncRequest.emit(this.user.id);
    console.log('inserted?');


  }

  delete(id: number) {
    const delURL = environment.apiUrl + '/' + id;
    console.log('deleting');
    console.log('deleteURL  ', delURL);
    this.http.delete(delURL).subscribe();
    this.getUser.userDeleted.emit(id);
    console.log('deleted');


  }

}
