import { Component, OnInit, Input } from '@angular/core';
import { AggiornaService } from '../aggiorna.service';

@Component({
  selector: 'app-view3',
  templateUrl: './view3.component.html',
  styleUrls: ['./view3.component.scss']
})
export class View3Component implements OnInit {
  @Input()
  data: string;
  constructor(private observer: AggiornaService) { }

  ngOnInit() {
    this.update();
  }
  update() {

    this.observer.syncRequest.subscribe((d: string) => { this.data = d; });
  }

  updateRequest() {
    this.observer.syncRequest.emit(this.data);
  }

}
